import React from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import Question from './Question';
import QuestionCount from './QuestionCount';
import AnswerOption from './AnswerOption';
import {Link} from 'react-router-dom';

import {Button, Icon } from 'react-materialize'

function Quiz(props) {

  function renderAnswerOptions(key) {
    return (
      <AnswerOption
        key={key.content}
        answerContent={key.content}
        answerType={key.type}
        answer={props.answer}
        questionId={props.questionId}
        questionInfo={props.questionInfo}
        onAnswerSelected={props.onAnswerSelected}
      />
    );
  }

  return (
    <div style={{minHeight: '70vh'}}>
      <ReactCSSTransitionGroup
        className="containerr"
        component="div"
        transitionName="fade"
        transitionEnterTimeout={800}
        transitionLeaveTimeout={500}
        transitionAppear
        transitionAppearTimeout={500}
      >
        <div key={props.questionId}>
          <QuestionCount
            counter={props.questionId}
            total={props.questionTotal}
          />
          <Question content={props.question} />
          <Question questionInfo={props.questionInfo}/>

          <ul className="answerOptions">
            {props.answerOptions.map(renderAnswerOptions)}
          </ul>
          {props.questionId===1 &&
          <div className="bordaquiz">
            <Link to='/'> <Button flat className="btnVoltar">Voltar<Icon left>arrow_back</Icon></Button> </Link>
          </div>
          }
          {props.questionId!==1 &&
          <div className="bordaquiz">
            <Button flat className="btnVoltar" onClick={props.handleBtnVoltar}>Voltar<Icon left>arrow_back</Icon></Button>
          </div>
          }
        </div>
      </ReactCSSTransitionGroup>
    </div>
  );
}

export default Quiz;



