import React, { Component } from 'react';
import {Link} from 'react-router-dom';

class CustomFooter extends Component{
  refreshPage(){
    //window.location.reload();
  }
  render(){
    return (
      <div id="footer">
        <div style={{backgroundColor:"#404040", height:"77px", margin:'auto', position:'sticky', bottom:0, right:0, left: 0}}>
          <div>
            <Link to='/creditos'> <img src="./Logo64_Branco.png" className="logofooter right" alt="LogoDAF4" onClick={this.refreshPage}/></Link>
            <Link to='/creditos' className="copyrighttext white-text right" style={{marginTop:"26px", fontWeight: 300}} onClick={this.refreshPage}>Desenvolvido por DAF-4</Link>
          </div>
        </div>
      </div>
    )
  }
}
export default CustomFooter;



